@mixin base() {
    display: block;
    color: white;
    background-color: $white;
    padding-left: 0;
    padding-right: 0;
    margin-bottom: 0;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;

    @media screen and (min-width: 768px) {
        display: flex;
        padding: 10px;
        align-items: center;
    }
}

.header {
    @include base;

    @import 'heading';

    &--alt {
        @include base;

        text-align: center;
    }
}