@mixin base {
    margin-top: 0;
    padding-top: 40px;
    color: $white;
    font-size: 28px;
    font-weight: 400;
    padding: 20px;
}

&__heading {
    @include base;

    &--alt {
        @include base;

        font-size: 24px;
        font-variant: small-caps;
        
        @media screen and (min-width: 768px) {
            &:after {
                content: '';
                height: 1px;
                width: 75%;
                background-color: #f4f4f4;
                opacity: 0.15;
                display: inline-block;
                margin-bottom: 5px;
                margin-left: 10px;

                @media screen and (min-width: 1000px) {
                    width: 90%;
                }
            }
        }
    }

    &--hidden {
        @include visually-hidden;
    }
}